.App {
  text-align: center;

}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: row;
}

.question-container {
  display: flex;
  justify-content: center;
  width: 70%;
  height: 80%;
}

.question-box {
  background-color: #f2f2f2;
  width: 40%;
  padding: 20px;
  margin: 5%;
  border-radius: 5px;
  text-align: center;
}

.question-text {
  font-size: 25px;
  font-weight: bold;
  margin-bottom: 10px;
  height: 50%;
}

.answers {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  padding: 5%;
  height: 40%;

}

.answer {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ffffff;
  border: 1px solid #cccccc;
  border-radius: 5px;
  padding: 10px 20px;
  margin: 5px;
  cursor: pointer;
  width: 40%;
  height: 40%;
  font-size: 25px;
}

